<template>
    <div class="layout-card-container" :class="[{small:small},name]">
        <slot name="test"></slot>
        <slot></slot>
    </div>
</template>

<script type="text/ecmascript-6">
export default {
    name: 'CardContainer',
    props: {
        small: Boolean,
        name: String
    }
};
</script>

<style lang="less" scoped>
    .layout-card-container{
        box-sizing: border-box;
        // overflow: hidden;
        background-color: #fff;
        padding: 24px 26px;
        box-shadow: 0 4px 20px 6px #e7efff;
        border-radius: 8px;
        margin-bottom: 20px;
    }
    .small{
        padding: 22px 46px;
    }
</style>



