import Vue from 'vue'
import Router from 'vue-router'
import store from '@/store/index';
import permissions from '@/router/permission';

// 登录页面
const Login = () =>
    import ('@/views/Login');


Vue.use(Router)

const routes = [{
    meta: {
        title: '登录',
    },
    path: '/',
    name: 'Login',
    component: Login
}];

// 初始化路由
const router = new Router({ routes })
    // 动态添加路由
router.addRoutes(permissions.getRouters())

// 判断是否有登录权限，以及是否登录
router.beforeEach((to, from, next) => {
    const { name, title } = to.meta;

    if (to.matched.some(res => res.meta.requireAuth)) {
        next();
    } else {
        next();
    }
})

//映射路由表到vuex
store.dispatch('getRouteOptions', permissions.getRouters());

export default router;


