<template>
    <div class="app-table">
        <!-- 顶部状态栏 -->
        <slot name="topBar"></slot>
        <!-- 表格工具栏 -->
        <div class="app-table-utils" v-if="utils">
            <el-row :gutter="0">
                <el-form class="app-util-form" :inline="true" @submit.native.prevent>
                    <template v-if="utils.cols && utils.cols[0]">
                        <el-col :span="utils.cols[0]" class="flex-container">
                            <el-form-item v-for="(item,index) in utils.left" :key="index" :label="item.label">
                                <slot :name="item.slot"></slot>
                            </el-form-item>
                        </el-col>
                    </template>
                    <template v-if="utils.cols && utils.cols[1]">
                        <el-col :span="utils.cols[1]" class="flex-container rh-text-right">
                            <el-form-item v-for="(item,index) in utils.right" :key="index" :label="item.label">
                                <slot :name="item.slot"></slot>
                            </el-form-item>
                        </el-col>
                    </template>
                </el-form>
            </el-row>
        </div>
        <!-- 表格状态栏 -->
        <slot name="tableBar"></slot>
        <!-- 普通表格 -->
        <el-table
            v-if="table.tableType === 1"
            :data="table.data"
            stripe>
            <el-table-column
                v-for="(col,k) in table.cols"
                :key="k"
                :show-overflow-tooltip="col.key !== 'operation' && !col.overflow"
                :align="col.align || 'center'"
                :width="col.width || 'auto'"
                :label="col.label">
                <template slot-scope="{row}">
                    <slot name="column" :data="{row,col}">
                        <!-- <template v-if="col.key === 'operation'">
                            <el-button type="text">操作按钮</el-button>
                            <el-button type="text">
                                <router-link class="app-link-to" :to="`/peoples/admin/logList`">跳转链接</router-link>
                            </el-button>
                        </template>
                        <template v-else>{{row[col.key] || '-'}} </template> -->
                    </slot>
                </template>
            </el-table-column>
        </el-table>
        <!-- 多类型表格 -->
        <el-table
            v-if="table.tableType === 2"
            :data="table.data"
            stripe>
            <template v-for="(col,k) in table.cols">
                <el-table-column
                    v-if="col.types && col.types.includes(include)"
                    :key="k"
                    :show-overflow-tooltip="col.key !== 'operation' && !col.overflow"
                    :align="col.align || 'center'"
                    :width="col.width || 'auto'"
                    :label="col.label">
                    <template slot-scope="{row}">
                        <slot name="column" :data="{row,col}">
                            <!-- <template v-if="col.key === 'operation'">
                                <el-button type="text">操作按钮</el-button>
                                <el-button type="text">
                                    <router-link class="app-link-to" :to="`/peoples/admin/logList`">跳转链接</router-link>
                                </el-button>
                            </template>
                            <template v-else>{{row[col.key] || '-'}} </template> -->
                        </slot>
                    </template>
                </el-table-column>
            </template>
        </el-table>
        <!-- 自定义表格 -->
        <template v-else-if="table.tableType == 3">
            <slot name="appTable" :data="{table}"></slot>
        </template>

        <el-pagination
            v-if="!table.hidePagination && table.total > 10"
            class="table-pagination"
            @current-change="handleCurrentChange"
            :current-page="table.page"
            :page-size="config.table.pagination.size"
            :page-sizes="config.table.pagination.sizes"
            :layout="config.table.pagination.layout"
            :total="table.total">
        </el-pagination>
        <!-- 其它组件 如表格的dialog -->
        <slot name="other"></slot>
    </div>
</template>
<script>
export default {
    data() {
        return {
            cols: [12, 12],
            demo: { //test params
                table: {
                    queryData: { //查询参数
                        keyWord: null,
                        page: 1,
                        limit: 10
                    },
                    data: [], //表格数据
                    total: 0, //总页数
                    page: 1, //页码索引
                    tableType: 3, //表格类型
                    hidePagination: true, //隐藏分页器
                    utils: { //表格工具栏
                        left: [{ label: '', slot: 'add' }],
                        right: [{ label: '', slot: 'search' }],
                        cols: [12, 12]
                    },
                    cols: [ //表格列配置
                        {
                            key: 'id',
                            label: '机构名称'
                        },
                        {
                            key: 'operation',
                            width: '200px',
                            label: '操作'
                        }
                    ]
                }
            }
        };
    },
    props: {
        utils: { //表格工具栏
            left: Array,
            right: Array,
            type: Object,
            cols: Object
            // default() {
            //     return {
            //         left: [{ label: '', slot: 'add' }],
            //         right: [{ label: '', slot: 'search' }],
            //         cols: [12, 12]
            //     };
            // }
        },
        table: {
            type: Object,
            data: [], //表格数据
            page: 1, //页码索引
            total: 0, //总页数
            cols: [], //表格列配置
            tableType: 1, //1 普通表格 2 多类型表格 3 自定义表格
            hidePagination: true, //隐藏分页器
            default() {
                return {
                    data: [],
                    page: 1,
                    total: 0,
                    cols: [{
                        key: '',
                        label: ''
                    }],
                    tableType: 1
                };
            }
        },
        include: null //表格多类型区分字段
    },
    computed: {
        config() {
            console.log(this.table);
            return this.$store.state.config;
        }
    },
    methods: {

        /**
         * 翻页
         */
        handleCurrentChange(page) {
            this.$emit(`pageChange`, page);
        }
    }
};
</script>
<style lang="less" scoped>
.app-table{
  margin: 20px 20px 30px 20px;
  .el-table::before {
    display: none;
  }
  /deep/ .el-table th{
    height:36px;
    font-weight:500;
    color:rgba(51,51,51,1);
    background:rgba(244,247,253,1);
    border-right: 1px dashed #E0E0E0;
    border-bottom: 1px dashed #E0E0E0;
    border-top: 1px dashed #E0E0E0;
    /deep/ .cell{
      font-size:14px;
      font-weight: bold;
    }
  }
  /deep/ .el-table tr th:first-child{
    border-left: 1px dashed  #E0E0E0;;
  }
  /deep/ .el-table tr td{
    height:36px;
    font-size:14px;
    color:rgba(102,102,102,1);
    background:#FFFFFF;
    border-right: 1px dashed #E0E0E0;
    border-bottom: 1px dashed #E0E0E0;
    /deep/ .el-button--text{
      color:rgba(102,102,102,1);
      font-size: 14px;
      font-family: 'PingFang-SC-Medium';
    }
     /deep/ .el-button--text:hover{
     text-decoration:underline;
    }
  }
  /deep/ .el-table tr td:first-child{
    border-left: 1px dashed  #E0E0E0;
  }

}




.table-pagination.el-pagination{
  margin-top: 20px;
  text-align: center;

  /deep/ .el-pagination__total{
    font-size: 14px;
    color:rgba(102,102,102,1);
  }
   /deep/ .btn-prev{
    font-size: 14px;
    color:rgba(102,102,102,1);
  }
   /deep/ .el-pager{
    font-size: 14px;;
    color:rgba(102,102,102,1);
    /deep/ .number {
      font-family: 'PingFang-SC-Medium';
      font-size: 14px;
    }
  }
   /deep/ .btn-next{
    font-size: 14px;;

    color:rgba(102,102,102,1);
  }
   /deep/ .el-pagination__jump{
    font-size: 14px;
    color:rgba(102,102,102,1);
    /deep/ .el-pagination__editor.el-input .el-input__inner {
      height: 21px;
    }
  }

}


</style>




