<template>
    <div class="app-table-utils" v-if="utils">
        <el-row :gutter="0">
            <el-form class="app-util-form" :inline="true" @submit.native.prevent>
                <template v-if="utils.cols && utils.cols[0]">
                    <el-col :span="utils.cols[0]" class="flex-container">
                        <el-form-item v-for="(item,index) in utils.left" :key="index" :label="item.label">
                            <slot :name="item.slot"></slot>
                        </el-form-item>
                    </el-col>
                </template>
                <template v-if="utils.cols && utils.cols[1]">
                    <el-col :span="utils.cols[1]" class="flex-container rh-text-right">
                        <el-form-item v-for="(item,index) in utils.right" :key="index" :label="item.label">
                            <slot :name="item.slot"></slot>
                        </el-form-item>
                    </el-col>
                </template>
            </el-form>
        </el-row>
    </div>
</template>
<script type="text/ecmascript-6">
export default {
    name: 'TableUtil',
    data () {
        return {
            cols: [12, 12]
        };
    },
    props: {
        utils: {
            left: Array,
            right: Array,
            cols: Array,
            type: Object,
            default() {
                return {
                    left: [{ label: '', slot: '' }],
                    right: [{ label: '', slot: '' }],
                    cols: [12, 12]
                };
            }
        }
    },
    computed: {
        config() {
            return this.$store.state.config;
        }
    }
};
</script>
<style lang="less" scoped>
.flex-container /deep/ .el-form-item{
  margin-right: 30px;
  // margin-bottom: 30px;
}
.el-form-item--mini.el-form-item {
  margin-bottom: 0px;
}
</style>




