import types from './types.js';
const mutations = {
    /**
     * 侧边栏导航
     */
    [types.SIDE_BAR_MENUS](state, data) {
        state.sideBarMenus = data;
    },


    /**
     * 路由表
     */
    [types.GET_ROUTE_OPTIONS](state, data) {
        state.routeOptions = data;
    }

};

export default mutations;


