/**
 *  整个项目路由设置
 */
const Layout = () => { return import ('@/components/Layout') };
const MidContainer = () => { return import ('@/components/MidContainer') };

//终端管理
const DeviceMange = () => { return import ('@/views/DeviceMange') };

//设备类型管理
const DeviceTypesMange = () => { return import ('@/views/DeviceTypesMange') };

//统计分析 - 设备型号
const DeviceType = () => { return import ('@/views/StatisticalAnalysis/DeviceType') };

//统计分析 - 分组
const Group = () => { return import ('@/views/StatisticalAnalysis/Group') };

//用户管理
const UserMange = () => { return import ('@/views/UserMange') };

//分组管理
const GroupMange = () => { return import ('@/views/GroupMange') };


//日志管理 - 系统日志
const SystemLog = () => { return import ('@/views/LogMange/SystemLog') };

//日志管理 - 操作日志
const OperateLog = () => { return import ('@/views/LogMange/OperateLog') };

//顶层配置
const TopDeploy = () => { return import ('@/views/TopDeploy') };


export default [{
    path: '/admin',
    name: 'admin',
    meta: {
        requireAuth: true, //菜单权限
        title: '管理系统', //菜单名
        icon: '', //菜单图标
        top: true //是否顶部菜单
    },
    redirect: "/admin/DeviceMange",
    component: Layout,
    children: [{
            meta: {
                title: '终端管理',
                requireAuth: true,
            },
            path: 'DeviceMange',
            name: 'DeviceMange',
            component: DeviceMange
        },
        {
            meta: {
                title: '设备型号管理',
                requireAuth: true,
            },
            path: 'DeviceTypesMange',
            name: 'DeviceTypesMange',
            component: DeviceTypesMange
        },
        {
            meta: {
                title: '统计分析',
                requireAuth: true,
            },
            path: 'statistical',
            component: MidContainer,
            children: [{
                meta: {
                    title: '设备型号',
                    requireAuth: true,
                },
                path: 'DeviceType',
                name: 'DeviceType',
                component: DeviceType
            }, {
                meta: {
                    title: '分组',
                    requireAuth: true,
                },
                path: 'Group',
                name: 'Group',
                component: Group
            }]
        }, {
            meta: {
                title: '用户管理',
                requireAuth: true,
            },
            path: 'UserMange',
            name: 'UserMange',
            component: UserMange
        }, {
            meta: {
                title: '分组管理',
                requireAuth: true,
            },
            path: 'GroupMange',
            name: 'GroupMange',
            component: GroupMange
        }, {
            meta: {
                title: '日志管理',
                requireAuth: true,
            },
            path: 'log',
            component: MidContainer,
            children: [{
                meta: {
                    title: '操作日志',
                    requireAuth: true,
                },
                path: 'OperateLog',
                name: 'OperateLog',
                component: OperateLog
            }, {
                meta: {
                    title: '系统日志',
                    requireAuth: true,
                },
                path: 'SystemLog',
                name: 'SystemLog',
                component: SystemLog
            }]
        },
        {
            meta: {
                title: '配置管理',
                requireAuth: true,
            },
            path: 'TopDeploy',
            name: 'TopDeploy',
            component: TopDeploy
        },
    ]
}];


