const getters = {
    /**
     * 配置文件
     * @param state
     * @return {*}
     */
    config(state) {
        return state.config;
    },

    /**
     * 侧边栏菜单
     * @param state
     * @return {Array}
     */
    sideBarMenus(state) {
        return state.sideBarMenus;
    },

    /**
     * 路由表
     * @param {*} state
     */
    routeOptions(state) {
        return state.routeOptions;
    }

};

export default getters;


