<template>
    <el-dialog
        v-if="dialogConf.isShow"
        :close-on-click-modal="dialogConf.closeOnClickModal || false"
        :width="dialogConf.width"
        :center="dialogConf.center"
        :append-to-body="dialogConf.appendToBody || false"
        :visible.sync="dialogConf.isShow"
        @closed="handleClosed"
        @opened="handleOpened"
    >
        <h1 slot="title" class="dialog-title">
            {{ dialogConf.title }}
        </h1>
        <slot />
        <!-- <el-row><el-col :span="24"><slot></slot></el-col></el-row> -->
    </el-dialog>
</template>
<script type="text/ecmascript-6">
export default {
    name: 'DialogContainer',
    props: {
        dialogConf: {
            type: Object,
            default() {
                return {
                    width: null,
                    isShow: false,
                    center: true,
                    title: null,
                    closeOnClickModal: true,
                    appendToBody: false
                };
            }
        }
    },
    methods: {
        handleClosed() {
            this.$emit(`closed`);
        },

        handleOpened() {
            this.$emit(`opened`);
        }
    }
};
</script>
<style lang="less" scoped>
.dialog-title{
    font-size: 18px;
}
</style>



