import util from '@/common/method/util';

export default {
    /**
     * 判断指静脉是否采集
     */
    isVien(value) {
        if (value && (value[0] || value[1])) {
            if (value[0].valid == 1 || value[1].valid == 1) {
                return '已验证';
            } else {
                return '未验证';
            }
        } else {
            return '未采集';
        }
    },

    /**
     * 判断指静脉是否采集
     */
    isPermission(value) {
        let permission = [];
        value.map(item => {
            if (item == 1) {
                permission.push('社保_应用系统管理员')
            } else if (item == 2) {
                permission.push('社保_应用管理员')
            } else if (item == 3) {
                permission.push('社保_副应用管理员')
            } else if (item == 4) {
                permission.push('社保_信息采集员')
            }
        });

        return permission.toString();
    },
};


