<template>
  <div id="app">
     <!--路由匹配到的组件将渲染在这里-->
      <transition name="fade" mode="out-in">
          <router-view></router-view>
      </transition>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style lang="less">
.el-header,.el-main{
  padding: 0;
}

.el-tab-pane /deep/ .app-table-utils{
  margin: 25px 0 25px 20px;
}

// 弹框样式
.el-message-box__wrapper {
  /deep/ .el-message-box__btns .el-button{
    width:120px;
    height:32px;
    background:rgba(243,243,243,1);
    border:1px solid rgba(221,221,221,1);
    border-radius:2px;
  }
  /deep/ .el-message-box__btns .el-button:hover{
    font-weight:400;
    color:rgba(51,51,51,1);
    opacity: 0.9;
  }

  /deep/ .el-message-box__btns .el-button--primary{
    width:120px;
    height:32px;
    background:rgba(54,123,246,1);
    border-radius:2px;
  }
  /deep/ .el-message-box__btns .el-button--primary:hover{
    font-weight:400;
    color:rgba(255,255,255,1);
    opacity: 0.8;
  }
}

.el-dialog /deep/ .el-dialog__header{
  height:40px;
  line-height: 40px;
  padding: 0;
  text-align: center;
  color: #fff;
  background:rgba(54,123,246,1);
  border-radius:2px 2px 0px 0px;
  /deep/ .el-dialog__headerbtn{
    top: 13px;
    /deep/ .el-icon-close:before {
      color: #fff;
    }
  }
}

// 表格暂无数据图片
.el-table__empty-text {
  display: block;
  width: 280px !important;
  height: 230px;
  margin-top: 190px;
  padding-top: 190px;
  background:url(../static/imgs/noData.png) no-repeat;
  box-sizing: border-box;
}


// 主要页面的查询框 + 按钮
.content_box{
  // width: 100%;
  min-height: calc(87vh);
  background:rgba(255,255,255,1);
  box-shadow:0px 1px 4px 0px rgba(147,150,152,0.1);
  border-radius:2px;
  /deep/ .app-table-utils{
    height: 70px;
    box-sizing: border-box;
    /deep/ .flex-container{
      margin: 20px 0 20px 20px;
    }
  }

  .queryBtn{
    width:100px;
    height:30px;
    background:rgba(54,123,246,1);
    border-radius:2px;
    font-size:14px;
    font-family: 'PingFang-SC-Medium';
    font-weight:400;
    color:rgba(255,255,255,1);
  }
  .queryBtn:hover{
    background:rgba(54,123,246,1);
    opacity: 0.8;
    color:rgba(255,255,255,1);
  }
}
</style>



