/**
 * 公有方法类
 */
export default {



    /**
     * 移除对象中的空字符串
     * @param test
     * @param recurse
     */
    deleteEmptyString(test, recurse) {
        for (let i in test) {
            if (test[i] === '') {
                delete test[i];
            } else if (recurse && typeof test[i] === 'object') {
                this.deleteEmptyString(test[i], recurse);
            }
        }
    },

    /**
     * 删除对象中的空Key
     * @param test
     * @param recurse
     */
    deleteEmptyObject(test, recurse) {
        for (let i in test) {
            if (this.isEmpty(test[i])) {
                delete test[i];
            } else if (recurse && typeof test[i] === 'object') {
                this.deleteEmptyObject(test[i], recurse);
            }
        }
    },

    /**
     * 移除对象中的无效属性
     * @param obj
     * @return {*}
     */
    removeEmpty(obj) {
        Object.keys(obj).forEach(function(key) {
            (obj[key] && typeof obj[key] === 'object') && this.removeEmpty(obj[key]) ||
                (obj[key] === undefined || obj[key] === null || obj[key] === '') && delete obj[key];
        });
        return obj;
    },

    /**
     * 深度拷贝
     * @param {*} obj
     */
    deepCloneObject(obj) {
        let objClone = Array.isArray(obj) ? [] : {};
        if (obj && typeof obj === 'object') {
            for (let key in obj) {
                if (obj.hasOwnProperty(key)) {
                    //判断ojb子元素是否为对象，如果是，递归复制
                    if (obj[key] && typeof obj[key] === 'object') {
                        objClone[key] = this.deepCloneObject(obj[key]);
                    } else {
                        //如果不是，简单复制
                        objClone[key] = obj[key];
                    }
                }
            }
        }
        return objClone;
    },

    /**
     * json对象转FormData
     * @param obj
     * @returns {*}
     */
    jsonToFormData(obj) {
        // 创建表单对象
        let formData = new FormData();
        if (obj) {
            for (let k in obj) {
                formData.append(k, obj[k]);
            }
        }
        return formData;
    },

    /**
     * 时间戳转日期格式
     * @return
     * */
    getFormattertCurrentTime(val) {
        if (!val) {
            val = new Date();
        }
        let date = new Date(val);
        let Y = date.getFullYear() + '-';
        let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        let h = date.getHours() < 10 ? '0' + date.getHours() + ':' : date.getHours() + ':';
        let m = date.getMinutes() < 10 ? '0' + date.getMinutes() + ':' : date.getMinutes() + ':';
        let s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        return Y + M + D + ' ' + h + m + s;
    },

    /**
     * 时间转换格式 yy-mm-dd hh:mm:ss
     * @return
     * */
    getFormattertTypeTime(val) {
        let date = new Date(val);
        let Y = date.getFullYear() + '-';
        let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();

        //当前时间的时分秒
        let currentTime = new Date();
        let h = currentTime.getHours() < 10 ? '0' + currentTime.getHours() + ':' : currentTime.getHours() + ':';
        let m = currentTime.getMinutes() < 10 ? '0' + currentTime.getMinutes() + ':' : currentTime.getMinutes() + ':';
        let s = currentTime.getSeconds() < 10 ? '0' + currentTime.getSeconds() : currentTime.getSeconds();
        return Y + M + D + ' ' + h + m + s;
    },


    /**
     * 时间戳转日期格式 YY-MM-DD
     * @return
     * */
    getFormattertTime(val) {
        if (!val) {
            val = new Date();
        }
        let date = new Date(val);
        let Y = date.getFullYear() + '-';
        let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        return Y + M + D;
    },


    /**
     * 验证身份证合法性
     */
    IdentityCodeValid(code) { //验证身份证合法性
        var city = { 11: "北京", 12: "天津", 13: "河北", 14: "山西", 15: "内蒙古", 21: "辽宁", 22: "吉林", 23: "黑龙江 ", 31: "上海", 32: "江苏", 33: "浙江", 34: "安徽", 35: "福建", 36: "江西", 37: "山东", 41: "河南", 42: "湖北 ", 43: "湖南", 44: "广东", 45: "广西", 46: "海南", 50: "重庆", 51: "四川", 52: "贵州", 53: "云南", 54: "西藏 ", 61: "陕西", 62: "甘肃", 63: "青海", 64: "宁夏", 65: "新疆", 71: "台湾", 81: "香港", 82: "澳门", 91: "国外 " };
        var tip = "";
        var pass = true;

        var patt1 = new RegExp("(^[1-9]\\d{5}(18|19|([23]\\d))\\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\\d{3}[0-9Xx]$)|(^[1-9]\\d{5}\\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\\d{2}$)");
        if (!code || !patt1.test(code)) {
            tip = "身份证号格式错误";
            pass = false;
        } else if (!city[code.substr(0, 2)]) {
            tip = "地址编码错误";
            pass = false;
        } else {
            //18位身份证需要验证最后一位校验位
            if (code.length == 18) {
                code = code.split('');
                //∑(ai×Wi)(mod 11)
                //加权因子
                var factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
                //校验位
                var parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2];
                var sum = 0;
                var ai = 0;
                var wi = 0;
                for (var i = 0; i < 17; i++) {
                    ai = code[i];
                    wi = factor[i];
                    sum += ai * wi;
                }
                var last = parity[sum % 11];
                if (parity[sum % 11] != code[17]) {
                    tip = "校验位错误";
                    pass = false;
                }
            }
        }
        return pass;
    }




};


